/* Big tablet to 1200 (width smaller than the 1140 pixel row) */

@media only screen and (max-width: 1200px) {
    .row, .container {
        padding: 0 2%;
    }
}

/* Small tablet to big tablet: 768px to 1023px */

@media only screen and (max-width: 1023px) {
    body {
        font-size: 18px;
    }
    section {
        padding: 60px 0;
    }
}

/* SMALL PHONES TO SMALL TABLETS: FROM 481px TO 767px */

@media only screen and (max-width: 767px) {
    body {
        font-size: 16px;
    }
    section {
        padding: 30px 0;
    }
    .row, .container {
        padding: 0 4%
    }
    .col {
        width: 100%;
        margin: 0 0 4% 0;
    }
    .main-nav {
        transition: height .25s ease;
        overflow: hidden;
    }
    .main-nav:not(.active) {
        display: none;
    }
    .mobile-nav-icon {
        display: inline-block;
    }
    .main-nav {
        /* float: left; */
        margin-top: 100px;
        margin-left: 10px
    }
    .main-nav li {
        display: block;
    }
    .main-nav li a:link, .main-nav li a:visited {
        display: block;
        border: 0;
        padding: 5px 0;
        font-size: 100%;
    }
    h1 {
        font-size: 180%;
    }
    h2 {
        font-size: 150%;
    }
}

/* SMALL PHONES: FROM 0 TO 480px */
@media only screen and (max-width: 480px) {
    section {
        padding: 25px 0;
    }
}