/* BASE GREEN:  #649979 */


/* ------------------------- */

/* BASIC SETUP */

/* ------------------------- */

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html,
body {
  background-image: linear-gradient(to right bottom, #2c854f, #207441);
  background-size: cover;
  background-repeat: no-repeat;
  min-height: 100vh;
  overflow-x: hidden;
  color: rgb(235, 235, 235);
  font-family: 'Asap', sans-serif;
  font-weight: 400;
  font-size: 20px;
  text-rendering: optimizeLegibility;
}

/* ------------------------- */

/* REUSABLE COMPONENTS */

/* ------------------------- */

.row {
  max-width: 1140px;
  margin: 0 auto;
}

section {
  padding: 80px 0;
}
